body {
  background-color: #4b92db;
  margin: 0;
  padding: 0;
}

.map-container {
  width: 100%;
  height: 100%;
  position: relative;
}

#wind-canvas, #arrow-canvas {
  pointer-events: none;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

#particle-canvas {
  z-index: 1;
}

#world-map {
  width: 100%;
  height: 100%;
  position: relative;
}

.scale-container {
  z-index: 2;
  flex-direction: column;
  align-items: center;
  padding: 5px 10px;
  font-family: system-ui;
  display: flex;
  position: absolute;
  bottom: 5%;
  left: 5%;
}

.scale-bar {
  background-color: #000;
  width: 64px;
  height: 6px;
  margin-bottom: 4px;
}

.scale-text {
  color: #000;
  font-size: 12px;
}

.loading-text {
  color: #000;
  z-index: 1000;
  font-family: system-ui;
  font-size: min(4vw, 24px);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wind-particle {
  pointer-events: none;
  background-color: #ffffffb3;
  border-radius: 50%;
  width: 2px;
  height: 2px;
  position: absolute;
  box-shadow: 0 0 2px #ffffff80;
}

#tooltip {
  color: #000;
  white-space: pre;
  pointer-events: none;
  z-index: 1001;
  -webkit-user-select: none;
  user-select: none;
  padding: 8px 12px;
  font-family: "Press Start 2P";
  font-size: 12px;
  line-height: 1.4;
  display: none;
  position: absolute;
}
